            style=""
            :items="table.scoreList"
            :fields="table.fields"
            :foot="table.last"
            label-size="lg"
            bordered
          >
            <template #head(keputusansss)="data">
              <span
                class="saj-title"
                style=""
              >{{ $t(data.label) }}</span>
            </template>
            <template #head(pemberattt)="data">
              <span
                class="saj-title"
                style=""
              >{{ $t(data.label) }}</span>
            </template>
            <template #head(markah)="data">
              <span
                class="saj-title"
                style=""
              >{{ $t(data.label) }}</span>
            </template>
            <template #head(markah_terkumpul)="data">
              <span
                class="saj-title"
                style=""
              >{{ $t(data.label) }}</span>
            </template>
            <template #head(markah_manager)="data">
              <span
                class="saj-title"
                style=""
              >{{ $t(data.label) }}</span>
            </template>
            <template #head(markah_terkumpul_manager)="data">
              <span
                class="saj-title"
                style=""
              >{{ $t(data.label) }}</span>
            </template>

            <template #cell(keputusansss)="data">
              {{ $t(data.item.keputusan) }}
              <div v-if="kra === 0">
                <div v-if="data.index === 0">
                  {{ $t("Overall Assessment") }}
                </div>
              </div>
              <div v-else>
                <div v-if="data.index === 0">
                  {{ $t('Key Result Area') }}
                </div>
              </div>
            </template>
            <template #cell(pemberattt)="data">
              <div v-if="data.index === 5">
                <span v-if="contributionPercent === 0 && contributionPercentTeam === 0">100%</span>
                <span v-else>100%</span>
              </div>
              <span v-else>{{ $t(data.item.pemberat) }}</span>

            </template>
          </b-table>

        </div>

        <div class="col-6 p-0 mt-1">
          <b-card
            no-body
            fluid="lg"
            class="d-flex justify-content-center"
            style="border-radius: 20px;"
          >
            <b-row class="d-flex m-0 mt-1 ml-1 mr-1 p-0 pt-1">
              <b-col
                cols="8"
                class="d-flex justify-content-start saj-title"
              >
                {{ $t('Previous score') }}:
              </b-col>
            </b-row>

            <b-card-body
              class="
              d-block
              justify-content-space-between
              align-items-center
              mt-1
            "
            >
              <b-row class="">
                <b-col
                  xl="6"
                  md="6"
                  class="text-center"
                >
                  <span
                    class="d-flex justify-content-center saj-header"
                    style=""
                  >{{ overall_total2 }}</span>
                  <div>
                    <b-card-text
                      class="saj-title"
                      style="color: #000000"
                    >
                      {{ $t('Self Score') }}
                    </b-card-text>
                  </div>
                </b-col>
                <b-col
                  xl="6"
                  md="6"
                  class="text-center"
                >
                  <span
                    class="d-flex justify-content-center saj-header"
                    style=""
                  >{{ overall_total_manager }}</span>
                  <div>
                    <b-card-text
                      class="saj-title"
                      style="color: #000000"
                    >
                      {{ $t('Manager Score') }}
                    </b-card-text>
                  </div>
                </b-col>
              </b-row></b-card-body>
          </b-card>
        </div>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

import {
  BTable,
  BCardBody,
  BCard,
  BCardText,
  BRow,
  BCol,
  BOverlay,
  BSpinner,
