} from "bootstrap-vue"
import axios from "axios"
// import grade from '@/component/grade-modal.vue'
// import editGrade from '@/views/pages/performance-statistics/edit_grade.vue'

export default {
  components: {
    BTable,
    BCardBody,
    BCard,
    BCardText,
    BRow,
    BCol,
    BOverlay,
    BSpinner,
    // BFormSelect,
  },

  props: {
    userID: {
      type: String,
      default: null,
    },
    performanceID: {
      type: String,
      default: null,
    },
    teamRole: {
      type: Number,
      default: 0,
    },
    gradeset: {
      type: Number,
      default: null,
    },
    subId: {
      type: Number,
      default: null,
    },
  },

  data() {
    return {
      show: true,
      kra: null,
      contribution: null,
      contributionPercent: 0,
      contributionPercentTeam: 0,
      scoreYearSelected: "a",
      scoreYearoptions: [
        { value: "a", text: "2022" },
        { value: "b", text: "2021" },
        { value: "c", text: "2020" },
      ],
      overall_total2: "",
      overall_total_manager: "",
      table: {
        // user score
        p1_kra: 0,
        p1_job_description: 0,
        p2_competency_core: 0,
        p2_competency_behaviour: 0,
        p3_training_development: 0,
        p3_contribution: 0,
        accumulative1: 0,
        accumulative2: 0,
        accumulative3: 0,
        accumulative4: 0,
        overall_total: 0,
        // manager score
        p1_kra_manager: 0,
        p1_job_description_manager: 0,
        p2_competency_core_manager: 0,
        p2_competency_behaviour_manager: 0,
        p3_training_development_manager: 0,
        p3_contribution_manager: 0,
        accumulative1_manager: 0,
        accumulative2_manager: 0,
        accumulative3_manager: 0,
        accumulative4_manager: 0,
        overall_total_manager: 0,
        bordered: true,

